"use client"

import { useState } from "react"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Loader2 } from "lucide-react"
import { toast } from "sonner"
import { createClient } from "@/lib/supabase/client"

interface EditChatNameDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  chatId: string
  currentName: string
  onSuccess?: () => void
}

export function EditChatNameDialog({
  open,
  onOpenChange,
  chatId,
  currentName,
  onSuccess,
}: EditChatNameDialogProps) {
  const [name, setName] = useState(currentName)
  const [saving, setSaving] = useState(false)
  const supabase = createClient()

  const handleSave = async () => {
    if (!name.trim()) {
      toast.error("O nome n√£o pode estar vazio")
      return
    }

    setSaving(true)
    try {
      const { error } = await supabase
        .from("chats")
        .update({ name: name.trim() })
        .eq("id", chatId)

      if (error) throw error

      toast.success("Nome atualizado com sucesso!")
      onSuccess?.()
      onOpenChange(false)
    } catch (error) {
      console.error("Erro ao atualizar nome:", error)
      toast.error("Erro ao atualizar o nome")
    } finally {
      setSaving(false)
    }
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>Editar Nome do Lead</DialogTitle>
          <DialogDescription>
            Altere o nome de exibi√ß√£o deste contato
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-4 py-4">
          <div className="space-y-2">
            <Label htmlFor="chat-name">Nome</Label>
            <Input
              id="chat-name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="Digite o nome do lead"
              disabled={saving}
            />
          </div>
        </div>

        <DialogFooter>
          <Button
            variant="outline"
            onClick={() => onOpenChange(false)}
            disabled={saving}
          >
            Cancelar
          </Button>
          <Button onClick={handleSave} disabled={saving}>
            {saving ? (
              <>
                <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                Salvando...
              </>
            ) : (
              "Salvar"
            )}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  )
}
